import editAddress from "@/components/editAddress/checkoutAddress";
export default {
  name: "checkoutLeft",
  components: {
    editAddress
  },
  props: {
    shopObj: {
      type: Object,
      value: null
    },
    addressDefault: {
      type: Object
    }
  },
  data() {
    return {
      selShow: false,
      selCountry: "",
      emailAddress: "",
      OrderNotes: "",
      OrderEmail: "",
      emailState: false,
      countryObj: [],
      editShow: false,
      addressType: 1,
      addressId: ""
    };
  },
  mounted() {
    if (this.$store.getters.getUserInfo) {
      this.$store.getters.getUserInfo.email
        ? (this.emailState = true)
        : (this.emailState = false);
    }
    if (this.$store.getters.getEmail) {
      this.OrderEmail = this.$store.getters.getEmail;
    }
  },
  methods: {
    goHome() {
      this.$router.push({
        path: "/"
      });
    },
    changeOrderEmail(e) {
      this.$bus.$emit("orderEmail", e.target.value);
    },
    addressDefaults() {
      this.$emit("getDefaultAdd");
    },
    editAddress(type, id) {
      this.editShow = true;
      this.addressType = type;
      this.addressId = String(id);
    }
  }
};
